import { useState } from "react";

function SearchForm() {
  //state set the information in an array
  //it imports the react use state useState is a library that comes with react that gives task more capabilities track variables that change dynamically tracks state of component
  const [task, setTask] = useState("");
  const [tasks, setTasks] = useState([]);
  //set initial state in useState

  function showSomething(evt) {
    setTask(evt.target.value);
    // Passing in the evt lets you access input value onChange
  }

  function doSomething() {
    // const allTasks = [...tasks, task]
    // const allTasks = [...tasks,task]
    //spread operator ...
    setTasks([task, ...tasks]);
    console.log(tasks);
  }

  // function showMoreText(){
  //   console.log("my friend");
  // }

  return (
    <div>
      <input
        type="text"
        placeholder="Enter Your Name:"
        onChange={showSomething}
      />
      <button onClick={doSomething}>Add This</button>
      <br />
      {tasks.map((aTask) => (
        <p>{aTask}</p>
      ))}
      <br />
    </div>
  );
}

export default SearchForm;
